/* intro-regular - latin_cyrillic */
@font-face {
  font-family: 'Intro';
  font-style: normal;
  font-weight: 400;
  src: url('./../fonts/Intro-Regular.eot'); /* IE9 Compat Modes */
  src: local(''),
       url('./../../fonts/Intro-Regular.eot') /* IE9 Compat Modes */, /* IE6-IE8 */
       url('./../fonts/Intro-Regular.woff') format('woff'), /* Modern Browsers */
       url('./../fonts/Intro-Regular.ttf') format('truetype'), /* Safari, Android, iOS */
}

/* intro-bold - latin_cyrillic */
